import React from 'react';
import { INoticia } from '../interfaces';

interface INoticiaProps {
  noticia: INoticia;
}
const Noticia: React.FunctionComponent<INoticiaProps> = ({ noticia }) => {
  const {
    author,
    title,
    content,
    description,
    publishedAt,
    source,
    url,
    urlToImage
  } = noticia;

  return <div>{title}</div>;
};

export default Noticia;
